.todoListTask{
    display: flex;
    align-items: center;
    border-bottom: 1px solid #d8d8d8;
    border-left: 2px solid transparent;
    height: 9vh;
    padding: 0 5%;
}

.todoListTask:hover{
    border-left: 2px solid #43a5fd;
}

.todoListTask input{
    display: flex;
    justify-content: start;
    align-items: center;
    margin-right: 5%;
}

