.done {
    opacity: 0.5;
    text-decoration: line-through;
    color: #cecfdd;
}

.todoListTaskDelete{
    font-weight: 700;
    height: 4vh;
    font-size: 1.2vw;
    background-color: transparent;
    border: none;
    color: #335272;
    outline: none;
    cursor: pointer;
    display: flex;
    justify-content: flex-end;
    width: 10%;
}

.todoListTaskDelete img{
    width: 100%;
}

.todoListTaskItem{
    width: 100%;
    height: 8vh;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 18px;
    font-weight: 600;
    color: #82829c;
}